.second-color {
  color:#c7d2db; 
}

.blue-color {
  color:#63a6e1; 
}

.dark-background {
  background: #222;
}

body {
  height: 100%;
}

.App {
  font-family: 'Ubuntu', sans-serif;
  background-color: #c7d2db;
  height:100%;
  width: 100%;
  text-align: center;
  display: inline-block;
}

button {
  font-family: 'Ubuntu', sans-serif;
  background: #c7d2db;
  border:none;
}

.orange {
  color:#bf4d28;
}

button:hover {
  background:#bf4d28;
  color: #c7d2db;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.square {
  width:15%;
  height:40px;
  margin-left:10px;
  margin-right:0px;
  font-size:18px;
}

.tab-row {
  margin-right: 0px; 
  padding-right: 0px;
  float: right;
  width: 60%;
}

.h1 {
  padding: 15px;
  text-align: center;
}

.bold {
  font-weight: bold; 
}


.element { 
  width: 100px; 
  height: 100px;
  border-radius: 10px;
}

.App-header {
  background-color: #222;
  height: 40px;
  padding: 20px;
  padding-right: 0px;
  color: white;
}

.left-header {
  display: inline-block;
  margin-top: -15px;
}

.logo {
  vertical-align:middle;
}

.title {
  color: #bf4d28;
  margin-left: 50px; 
  font-size: 18px;
  vertical-align: middle;
  padding-top: 5px;
}

.footer {
  padding: 15px;
  font-size: 18px; 
  display:-ms-flexbox; 
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.column {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  width: 25%;
  margin: 0 auto;
}

.footer-name {
  display: block;
}

.footer-contact {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}

.block {
  display:block; 
}

.block.footermark {
  padding-left: 15px; 
}

.contactitem.small {
  padding: 10px;
  display: inline-block;
  width: 30%;
}

.footerlogo{
  width: 300px; 
  padding:15px;
  padding-left: 0px;
  
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.Team-Header {
  font-size: 70px; 
  text-align: center;
  padding: 10px;
  width:1000px;
  margin-left:auto;
  margin-right:auto;
}

.People {
  display: inline-block;
  width:1000px;
  margin-left:auto;
  margin-right:auto;
  padding-bottom: 30px;
  padding-top: 20px;
}

.Person {
  width: 17%;
  margin: 20px;
  padding: 20px;
  padding-top: 10px; 
  padding-bottom: 10px; 
  display: block;
  float: left;
}


.person-img {
  height: 30px;
  width: 30px; 
  float: left;
}

.person-img:hover {
  border-radius: 50%;
  border-style: solid; 
  border-width: 2px; 
  border-color: #bf4d28;
  height: 26px; 
  width: 26px;
}

.person-item {
  text-align:center;
  padding-top: 10px;
}

.person-contact-items {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}

.person-contact-item {
  float: left;
  padding: 10px;
}

.person-title {
  font-size: 12px;
}

.profile {
  height: 120px; 
  width: 120px; 
  border-radius: 50%;
}

.person-name {
  font-size: 16px;
}

.big-icons {
  height: 30px;
  width: 30px;
}

.big-profile {
  border: solid 2px #bf4d28;
  height: 116px; 
  width: 117px;
}

.mainpage-coverphoto {
  width: 240px;
  height:400px;
}

.Team-Header {
  
  background-size: 100%; 
  width:940px;
  height:600px;
  margin-left:auto;
  margin-right:auto;
  text-align:center;
}


.team-cover-text {
  width: 940px;
  display:inline-block;
  background-color: none;
  color:#bf4d28; 
  padding-top: 30px; 
  padding-bottom: 30px;
  margin-left:auto; 
  margin-right:auto;
  margin-top:30px;
  margin-bottom:auto;
  font-size:50px;
}

.events-cover-text {
  width: 940px;
  display:inline-block;
  background-color: none;
  color:#bf4d28; 
  padding-top: 30px; 
  padding-bottom: 30px;
  margin-left:auto; 
  margin-right:auto;
  margin-top:30px;
  margin-bottom:auto;
  font-size:50px;
}

.coverphototeam {
  width: 500px;
  height: 500px;
  border-radius: 50%;
  -webkit-transition: hidden 5s; /* Safari */
  -o-transition: hidden 5s;
  transition: hidden 5s;
}

.event-img {
  height: 200px;
  width: 250px;
  float: left;
}

.events {
  display: inline-block;
}

.Event {
  display: inline-block;
  width: 850px;
  background: #222;
  margin: 0 auto; 
  margin-bottom: 20px;
  padding: 20px;
}

.event-info { 
  margin: 0 auto;
  overflow:hidden;
  text-align: left;
  padding-left: 20px;
}

.event-title {
  font-size: 22px;
  font-weight: bold;
}

.event-item {
  padding-bottom: 10px; 
}

.event-desc {
  text-align: left;
}

.about-item-ev {
  height:20px; 
  width:20px;
  padding-right:15px;
  padding-bottom:0px;
  padding-top: 0px;
  vertical-align:middle;
}

.past-event {
  background: rgb(167, 167, 167); 
  color: #bf4d28;
}

.future-event {
  background: #222;
  color: #63a6e1;
}

.events-tags {
  padding-bottom: 20px; 
}

.events-tag-button {
  height: 40px; 
  width: 80px; 
  font-size: 18px; 
}

.clickedButton {
  background:#bf4d28;
  color: #c7d2db;
}

.popup-content {
  width: 100%;  
  height: auto;
  overflow:auto;
  background:#c7d2db;
  color: #bf4d28;
  padding: 20px; 
  display: block; 
  font-size: 15px;
}

.popup-content h1 {
  width: 80%;
  margin: 0 auto; 
  border-bottom: 1px solid gray;
  font-size: 18px;
  text-align: center;
  padding: 5px;
  padding-bottom: 10px; 
  margin-bottom: 20px; 
}

.event-img-popup {
  height: 200px; 
  padding-bottom: 10px; 
  padding-top: 10px; 
}

.popup-event-items {
  display: inline-block; 
  width: 83%;
  border-bottom: 1px solid gray;
  margin-bottom: 10px; 
}

.event-item-p {
    padding-bottom: 10px; 
    width: 32%;
    float: left;
}

p {
  margin: 0 auto; 
  padding-top: 5px; 
  padding-bottom: 5px; 
  text-align: left;
  width: 100%;
}

.gallery-cover-text {
  width: 70%;
  display:inline-block;
  background-color: none;
  color:#bf4d28; 
  padding-top: 30px; 
  padding-bottom: 30px;
  margin-left:auto; 
  margin-right:auto;
  margin-top:auto;
  margin-bottom:auto;
  font-size:50px;
} 

.Gallery-div {
  width: 100%;
  margin: 0 auto;
  padding-bottom: 30px; 

}

.galpg {
  overflow: hidden;
}

.ReactGridGallery {

  width: 78%; 
  margin: 0 auto;
}

.gallery-tag-button {
  height: 25px; 
  width: 50px; 
  font-size: 12px; 
}

.clickedButton {
  background:#bf4d28;
  color: #c7d2db;
}

.gallery-pages {
  padding-bottom: 20px; 
}

.footer { 
  width: 100%;
}

@media only screen and (max-width: 1000px) {
  .gallery-cover-text {
    font-size: 40px; 
  }
}

@media only screen and (max-width: 700px) {
  .gallery-cover-text {
    font-size: 25px; 
  }
}


